import React, { useState, useEffect } from 'react'

import style from './payments.module.css'

import Layout from '../components/Layout'
import SEO from '../components/SEO'

const CHECKOUT_ITEMS = {
  half: 'sku_EbbNcAGW78YUXY',
  full: 'sku_Ebc7E9vpWYFxZV',
}

const PageTemplate = () => {
  const [submit, setSubmit] = useState('')
  const [stripe, setStripe] = useState()

  useEffect(() => {
    const prodStripe = window.Stripe('pk_live_PBQQ1XC37eg0LXG6Ws28i6po', {
      betas: ['checkout_beta_4'],
    })

    const testStripe = window.Stripe('pk_test_UI8KatjjVPUDJs4gnuuat3VZ', {
      betas: ['checkout_beta_4'],
    })

    setStripe(testStripe)
  }, [])

  useEffect(
    () => {
      if (submit) {
        stripe
          .redirectToCheckout({
            items: [{ sku: submit.sku, quantity: 1 }], // test sku
            //items: [{ sku: "sku_EbbApHjsnlwtNT", quantity: 1 }],

            // Note that it is not guaranteed your customers will be redirected to this
            // URL *100%* of the time, it's possible that they could e.g. close the
            // tab between form submission and the redirect.
            successUrl: 'https://the-initiative-code.github.io/success',
            cancelUrl: 'https://the-initiative-code.github.io/pricing',
          })
          .then(function(result) {
            if (result.error) {
              // If `redirectToCheckout` fails due to a browser or network
              // error, display the localized error message to your customer.
              var displayError = document.getElementById('error-message')
              displayError.textContent = result.error.message
            }
            setSubmit(false)
          })
          .catch(e => {
            alert(e)
          })
      }
    },
    [submit]
  )

  return (
    <Layout current="/payments">
      <SEO title="payments" />
      <div className={style.frame}>
        <div className={style.paymentsFrame}>
          <h1 className={style.pageTitle}>
            Payment{' '}
            <small>
              <i>(beta)</i>
            </small>
          </h1>
          <br />
          <div>
            <h5>
              So you've spoken with us<span className="text-highlight">*</span>{' '}
              and agreed upon a price.
              <sup className="text-highlight">#</sup>
            </h5>

            <p>
              Brand Design Consultation
              <br />
              Website Hosting
              <br />
              Website Design + 2 Iterations
            </p>
            <br />
            <h4>Please pick an option :</h4>
            <br />
            <p>50% upfront with 50% upon completion</p>
            <button
              className={style.submitPay}
              onClick={() => setSubmit({ sku: CHECKOUT_ITEMS.half })}
            >
              Pay Upfront 150 USD
            </button>
            <br />

            <br />
            <p>
              <span className="text-highlight">
                <strong>Full</strong>
              </span>{' '}
              amount of 300 USD
            </p>
            <button
              className={style.submitPay}
              onClick={() => setSubmit({ sku: CHECKOUT_ITEMS.full })}
            >
              Pay Full 300 USD
            </button>
            <br />
            <br />
            <br />
            <strong>
              <span className="text-highlight">*</span>
            </strong>
            <i>
              <small>
                {' '}
                Actually, if you haven't been in contact with us yet, please
                speak to us first.{' '}
              </small>
            </i>
            <br />
            <br />
            <strong>
              <sup className="text-highlight">#</sup>
            </strong>
            <i>
              <small>
                Of course, we are fans of free money,
                <br /> but just we want to make sure you're knowingly doing so.
                *wink
              </small>
            </i>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default PageTemplate
